import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Dashboard.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Dashboard',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/SpaceWarsPrivacyPolicy',
    name: 'SpaceWarsPrivacyPolicy',
    component: () => import(/* webpackChunkName: "about" */ '../views/SpaceWarsPrivacyPolicy.vue')
  },
  {
    path: '/SpaceWarsTermsAndConditions',
    name: 'SpaceWarsTermsAndConditions',
    component: () => import(/* webpackChunkName: "about" */ '../views/SpaceWarsTermsAndConditions.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
